  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding: 40px 15px;
  background: linear-gradient(rgb(255, 255, 255), #48798e);
  color: rgb(0, 0, 0);
  letter-spacing: 0.5px;
}

/*? **\`\` Form search bar section */
/* ***************************************************** */
#search-input {
  padding: 5px 10px;
  border-radius: 50px;
  width: clamp(3vw, 350px, 80vw);
  text-align: center;
}

#search-input::placeholder {
  text-align: center;
  opacity: 1;
}

#search-input:focus::placeholder {
  opacity: 0;
  transition: opacity 500ms;
}

#submit-button {
  background: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
  left: -40px;
  top: -18px;
  cursor: pointer;
  user-select: none;

  img {
    position: absolute;
    width: 25px;
    transform: scaleX(-1);
  }
}

/*? **\`\` Form C or F toggle section */
/* ***************************************************** */
#toggle-wrapper {
  display: flex;
  position: relative;
  bottom: -10px;
  font-size: 1.5rem;
}

#C-toggle,
#F-toggle {
  appearance: none;

  + label {
    color: rgb(181, 181, 181);
  }
}

#C-toggle:checked + label,
#F-toggle:checked + label {
  color: black;
  transition: color 500ms;
}

/*? **\`\` Main section */
/* ***************************************************** */
main {
  display: grid;
  grid-template-rows: 1fr 2fr;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  min-width: 60vw;
  height: 60vh;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 10px 0;
}

/*? **\`\` City section */
/* ***************************************************** */
#city-wrapper {
  grid-column: 3 / -1;
}

.city_name {
  font-size: clamp(3vw, 3rem, 1.25rem);
  margin-bottom: 10px;
  font-weight: bold;
}

.region {
  margin-bottom: 5px;
}

/*? **\`\` Current weather section */
/* ***************************************************** */
#current-weather-wrapper {
  grid-column: 2 / 3;

  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  column-gap: 10px;
  padding: 10px 0;

  .temp_f,
  .temp_c {
    font-size: clamp(3vw, 3rem, 1.25rem);
    font-weight: bold;
    grid-column: 3 / -1;
    align-self: end;
  }

  .condition {
    grid-row: 2 / 3;
    grid-column: 2 / 3;
    position: relative;
    top: 13px;
    justify-self: center;
    text-align: center;
  }

  .icon {
    grid-row: 1 / 2;
    grid-column: 2 / 3;
  }
}

/*? **\`\` Forecast weather section */
/* ***************************************************** */
#forecast-weather-wrapper {
  grid-column: 1 / -1;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
}

.forecast-day {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(15, minmax(19px, 1fr));
  padding: 10px;
}

.forecast-day:nth-child(2) {
  border-image: linear-gradient(#00000000, black, #00000000) 1;
  border-width: 0 2px;
  border-style: solid;
}

.date {
  font-weight: bold;
  font-size: 1.25rem;
  grid-row: 3 / 5;
  grid-column: 1 / -1;
}

.maxtemp_c,
.maxtemp_f {
  font-size: clamp(1.25rem, 3.75vw, 1.5rem);
  grid-row: 6 / 8;
  grid-column: 2 / 3;
}

.mintemp_c,
.mintemp_f {
  font-size: clamp(1.25rem, 3.75vw, 1.5rem);
  position: relative;
  top: -15px;
  grid-row: 8 / 10;
  grid-column: 2 / 3;
}

.forecast-day > .condition {
  grid-row: 9 / 11;
  grid-column: 1 / 2;
  text-align: center;
}

.forecast-day > .icon {
  grid-row: 5 / -1;
  grid-column: 1 / 2;
}

.daily_chance_of_rain {
  grid-row: 11 / 12;
  grid-column: 1 / 2;
}

.totalprecip_in,
.totalprecip_mm {
  grid-row: 12 / 13;
  grid-column: 1 / 2;
}

.daily_chance_of_snow {
  grid-row: 11 / 12;
  grid-column: 2 / 3;
}

.totalsnow_in,
.totalsnow_cm {
  grid-row: 12 / 13;
  grid-column: 2 / 3;
}

/*? **\`\` Multi city popup */
/* ***************************************************** */
.multi-city-wrapper {
  background: white;
  padding: 5px 18px;
  border-radius: 10px;
  line-height: 1.25rem;
  max-width: 80vw;
  z-index: 1;
  position: absolute;
  top: 65px;
}

.city-choice:not(.city-choice:last-child) {
  border-bottom: 1px solid #000;
}

.city-choice:last-child {
  color: #48798e;
}

.city-choice:hover:not(.city-choice:last-child) {
  background: rgba(72, 121, 142, 0.35);
  border-radius: 5px;
  margin: 0 -10px;
  padding: 0 10px;
  cursor: pointer;
}

/*? **\`\` Footer */
/* ***************************************************** */
footer {
  color: white;
  min-width: 60vw;
  display: flex;
  justify-content: space-between;
  gap: 40px;
  align-items: center;

  a:link,
  a:hover,
  a:active,
  a:visited {
    color: #aae5fe;
  }
}

#api-tag {
  display: flex;
  align-items: center;
  gap: 10px;

  img {
    background: rgba(255, 255, 255, 0.6);
    padding: 10px;
    border-radius: 5px;
  }
}

/*? **\`\` Loading image */
/* ***************************************************** */
#loading-image {
  position: absolute;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  z-index: 2;
  display: none;

  img {
    background: rgb(255, 255, 255);
    border: 5px solid #000;
    border-radius: 50%;
    width: clamp(250px, 50vw, 400px);
    transform: scale(0);
    transition: transform 500ms ease-in-out;

    &.loaded {
      transform: scale(1);
    }
  }
}

/*? **\`\` Pseudo element symbols and such */
/* ***************************************************** */
.temp_c::after,
.temp_f::after {
  content: '°';
}

.mintemp_c::after,
.mintemp_f::after {
  content: '° low';
}

.maxtemp_c::after,
.maxtemp_f::after {
  content: '° high';
}

.daily_chance_of_rain::after {
  content: '% chance of rain';
}

.daily_chance_of_snow::after {
  content: '% chance of snow';
}

.totalprecip_in::after,
.totalsnow_in::after {
  content: ' in';
}

.totalprecip_mm::after {
  content: ' mm';
}

.totalsnow_cm::after {
  content: ' cm';
}

/*? **\`\` This is controlled by javascript */
/* ***************************************************** */
.measure-hidden {
  display: none;
}

.weather-hidden {
  display: none;
}

.irrelevant-hidden {
  display: none;
}

/*? **\`\` Media queries */
/* ***************************************************** */
@media (max-width: 1100px) {
  body {
    height: 100%;
  }

  form {
    padding-bottom: 30px;
  }

  main {
    height: auto;
    grid-template-rows: 192px auto;
  }

  #current-weather-wrapper {
    grid-column: 1 / 3;

    .temp_f,
    .temp_c {
      align-self: center;
    }
  }

  #forecast-weather-wrapper {
    grid-template-columns: initial;
    grid-template-rows: repeat(3, 1fr);
    width: 100%;
    margin: auto;
  }

  .forecast-day:nth-child(2) {
    border-image: linear-gradient(to right, #00000000, black, #00000000) 1;
    border-width: 2px 0 0 0;
    border-style: solid;
  }

  .forecast-day {
    border-image: linear-gradient(to right, #00000000, black, #00000000) 1;
    border-width: 2px 0 0 0;
    border-style: solid;
  }

  footer {
    padding-top: 40px;
    flex-direction: column;
  }
}

@media (max-width: 400px) {
  #current-weather-wrapper {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    column-gap: 10px;
    padding: 10px 0;

    .temp_f,
    .temp_c {
      grid-column: 2 / -1;
    }

    .condition {
      grid-row: 2 / 3;
      grid-column: 1 / 2;
    }

    .icon {
      grid-row: 1 / 2;
      grid-column: 1 / 2;
    }
  }

  .daily_chance_of_rain {
    grid-row: 11 / 12;
    grid-column: 1 / -1;
  }

  .totalprecip_in,
  .totalprecip_mm {
    grid-row: 12 / 13;
    grid-column: 1 / -1;
  }

  .daily_chance_of_snow {
    grid-row: 14 / 15;
    grid-column: 1 / -1;
  }

  .totalsnow_in,
  .totalsnow_cm {
    grid-row: 15 / 16;
    grid-column: 1 / -1;
  }
}
