n {\n  display: none;\n}\n\n.weather-hidden {\n  display: none;\n}\n\n.irrelevant-hidden {\n  display: none;\n}\n\n/*? **`` Media queries */\n/* ***************************************************** */\n@media (max-width: 1100px) {\n  body {\n    height: 100%;\n  }\n\n  form {\n    padding-bottom: 30px;\n  }\n\n  main {\n    height: auto;\n    grid-template-rows: 192px auto;\n  }\n\n  #current-weather-wrapper {\n    grid-column: 1 / 3;\n\n    .temp_f,\n    .temp_c {\n      align-self: center;\n    }\n  }\n\n  #forecast-weather-wrapper {\n    grid-template-columns: initial;\n    grid-template-rows: repeat(3, 1fr);\n    width: 100%;\n    margin: auto;\n  }\n\n  .forecast-day:nth-child(2) {\n    border-image: linear-gradient(to right, #00000000, black, #00000000) 1;\n    border-width: 2px 0 0 0;\n    border-style: solid;\n  }\n\n  .forecast-day {\n    border-image: linear-gradient(to right, #00000000, black, #00000000) 1;\n    border-width: 2px 0 0 0;\n    border-style: solid;\n  }\n\n  footer {\n    padding-top: 40px;\n    flex-direction: column;\n  }\n}\n\n@media (max-width: 400px) {\n  #current-weather-wrapper {\n    grid-template-rows: repeat(2, 1fr);\n    grid-template-columns: repeat(2, 1fr);\n    column-gap: 10px;\n    padding: 10px 0;\n\n    .temp_f,\n    .temp_c {\n      grid-column: 2 / -1;\n    }\n\n    .condition {\n      grid-row: 2 / 3;\n      grid-column: 1 / 2;\n    }\n\n    .icon {\n      grid-row: 1 / 2;\n      grid-column: 1 / 2;\n    }\n  }\n\n  .daily_chance_of_rain {\n    grid-row: 11 / 12;\n    grid-column: 1 / -1;\n  }\n\n  .totalprecip_in,\n  .totalprecip_mm {\n    grid-row: 12 / 13;\n    grid-column: 1 / -1;\n  }\n\n  .daily_chance_of_snow {\n    grid-row: 14 / 15;\n    grid-column: 1 / -1;\n  }\n\n  .totalsnow_in,\n  .totalsnow_cm {\n    grid-row: 15 / 16;\n    grid-column: 1 / -1;\n  }\n}\n"],"sourceRoot":""}]);
